const positionInZone = (x, y, zoomTransform) => {
  x = (x - zoomTransform.x) / zoomTransform.k;
  y = (y - zoomTransform.y) / zoomTransform.k;
  return { x: x, y: y };
};

const estimateTextWidth = (text, size) => {
  const canvas = document.createElement("canvas");
  const context = canvas.getContext("2d");
  context.font = `${size}px sans-serif`;
  return context.measureText(text).width;
};

const testOverlap = (area, xmin, ymin, xmax, ymax) => {
  let width, height;
  if ("content" in area) {
    const s = area.size || 16;
    width = estimateTextWidth(area.content, s);
    height = area.size || 16;
  } else {
    width = area.width;
    height = area.height;
  }
  const stX = area.position.x - width / 2;
  const edX = area.position.x + width / 2;
  const stY = area.position.y - height / 2;
  const edY = area.position.y + height / 2;

  if (xmax < stX || edX < xmin) return false;
  if (ymax < stY || edY < ymin) return false;
  return true;
};

const textBBox = (text) => {
  const { content, size } = text;
  const width = estimateTextWidth(content, size);
  const abox = {
    x: text.position.x,
    y: text.position.y - size,
    width: width,
    height: size,
  };
  return abox;
};

const imageBBox = (image) => {
  const { width, height, scale } = image;
  const abox = {
    x: image.position.x,
    y: image.position.y,
    width: width * scale,
    height: height * scale,
  };
  console.log(abox);
  return abox;
};

export { positionInZone, estimateTextWidth, testOverlap, textBBox, imageBBox };
