import { useState } from "react";
import { Box, TextField, Typography } from "@mui/material";
import { useDispatch, useSelector } from "react-redux";
import { modifyPlan } from "../../store/reducers/plan.reducer";

const CanvasPanel = () => {
  const { size } = useSelector((state) => state.plan);
  const dispatch = useDispatch();
  const [currentWidth, setCurrentWidth] = useState(size.width);
  const [currentHeight, setCurrentHeight] = useState(size.height);
  return (
    <Box
      sx={{
        margin: 2,
        padding: 2,
        border: "1px solid gray",
        borderRadius: 1,
        display: "flex",
        flexDirection: "column",
        gap: 2,
      }}
    >
      <Typography variant="h6">Canvas Option</Typography>
      <TextField
        label="Width"
        variant="standard"
        type="number"
        value={currentWidth}
        onChange={(e) => {
          setCurrentWidth(e.target.value);
          dispatch(modifyPlan({ size: { width: parseInt(e.target.value) } }));
        }}
      />
      <TextField
        label="Height"
        variant="standard"
        type="number"
        value={currentHeight}
        onChange={(e) => {
          setCurrentHeight(e.target.value);
          dispatch(modifyPlan({ size: { height: parseInt(e.target.value) } }));
        }}
      />
    </Box>
  );
};

export default CanvasPanel;
