import React from "react";
import { useSelector } from "react-redux";
import ImageComponent from "../components/ImageComponent";

const ImageLayer = () => {
  const { images } = useSelector((state) => state.image);

  return (
    <>
      {images &&
        images.map((image) => (
          <ImageComponent
            key={image.uuid}
            {...image}
          />
        ))}
    </>
  );
};

export default ImageLayer;
