import { createSlice } from "@reduxjs/toolkit";
import * as d3 from "d3";
import _ from "lodash";

const initialState = {
  size: {
    width: 900,
    height: 500,
  },
  point: {
    x: 0,
    y: 0,
  },
  backgroundColor: "#fff",
  backgroundURL: "",
  zoomTransform: d3.zoomTransform({ k: 1, x: 0, y: 0 }),
  // zoomTransform: { k: 1, x: 0, y: 0 },
  selection: [],
  tool: "text",
};

export const planSlice = createSlice({
  name: "plan",
  initialState,
  reducers: {
    modifyPlan: (state, action) => {
      _.merge(state, action.payload);
    },
    setSelection: (state, action) => {
      const { uuids } = action.payload;
      state.selection = [];
      for (const uuid of uuids) {
        if (!state.selection.includes(uuid)) state.selection.push(uuid);
      }
    },
    changeTool: (state, action) => {
      state.tool = action.payload;
    },
  },
});

export const { modifyPlan, setSelection, changeTool } = planSlice.actions;

export default planSlice.reducer;
