import React from "react";
import * as d3 from "d3";
import {
  FitScreen,
  Image,
  TextFormat,
  ZoomIn,
  ZoomOut,
  HighlightAlt,
} from "@mui/icons-material";
import { Box, AppBar, Toolbar, IconButton, Divider } from "@mui/material";
import { useDispatch, useSelector } from "react-redux";
import { changeTool } from "../store/reducers/plan.reducer";
import { useSvg } from "../provider/SvgContext";

export default function ButtonAppBar({ onZoomIn, onZoomOut, onFitScreen }) {
  const dispatch = useDispatch();
  const { tool, zoomTransform } = useSelector((state) => state.plan);
  const svgRef = useSvg();

  const iconButtonStyle = (cur) => ({
    mr: 2,
    backgroundColor: tool === cur ? "rgba(255,255,255,0.3)" : "inherit",
  });

  return (
    <Box sx={{ flexGrow: 1, zIndex: 1100, position: "relative" }}>
      <AppBar
        position="static"
        sx={{ height: 65 }}
      >
        <Toolbar>
          <IconButton
            size="large"
            edge="start"
            color="inherit"
            aria-label="menu"
            sx={iconButtonStyle("select")}
            onClick={() => dispatch(changeTool("select"))}
          >
            <HighlightAlt />
          </IconButton>
          <IconButton
            size="large"
            edge="start"
            color="inherit"
            aria-label="menu"
            sx={iconButtonStyle("text")}
            onClick={() => dispatch(changeTool("text"))}
          >
            <TextFormat />
          </IconButton>
          <IconButton
            size="large"
            edge="start"
            color="inherit"
            aria-label="menu"
            sx={iconButtonStyle("image")}
            onClick={() => dispatch(changeTool("image"))}
          >
            <Image />
          </IconButton>
          <IconButton
            size="large"
            edge="start"
            color="inherit"
            aria-label="menu"
            sx={{ mr: 2 }}
            onClick={onZoomOut}
          >
            <ZoomOut />
          </IconButton>
          <IconButton
            size="large"
            edge="start"
            color="inherit"
            aria-label="menu"
            sx={{ mr: 2 }}
            onClick={onFitScreen}
          >
            <FitScreen />
          </IconButton>
          <IconButton
            size="large"
            edge="start"
            color="inherit"
            aria-label="menu"
            sx={{ mr: 2 }}
            onClick={onZoomIn}
          >
            <ZoomIn />
          </IconButton>
          {/* <Typography
            variant="h6"
            component="div"
            sx={{ flexGrow: 1 }}
          ></Typography>
          <Button color="inherit">Login</Button> */}
        </Toolbar>
      </AppBar>
    </Box>
  );
}
