import { createSlice } from "@reduxjs/toolkit";
import _ from "lodash";

const initialState = {
  texts: [],
};

/*

    {
      uuid: "",
      id: "",
      content: "",
      size: 0,
      color: "#fff",
      opacity: 1,
      position: {
        x: 0,
        y: 0,
      },
      type: "",
      space: 0,
      wrap: false,
    },

*/

export const textSlice = createSlice({
  name: "text",
  initialState,
  reducers: {
    addText: (state, action) => {
      const newText = action.payload;
      state.texts.push(newText);
    },
    removeText: (state, action) => {
      const { uuid } = action.payload;
      state.texts = state.texts.filter((text) => text.uuid !== uuid);
    },
    modifyText: (state, action) => {
      const { uuid, ...update } = action.payload;
      state.texts = state.texts.map((item) =>
        item.uuid === uuid ? _.merge({}, item, update) : item
      );
    },
  },
});

export const { addText, removeText, modifyText } = textSlice.actions;

export default textSlice.reducer;
