const TextComponent = ({
  id,
  content,
  size,
  color,
  opacity,
  position,
  type,
}) => (
  <text
    id={id}
    fontSize={size}
    color={color}
    opacity={opacity}
    x={position.x}
    y={position.y}
    fontFamily={type}
  >
    {content}
  </text>
);
export default TextComponent;
