import { useState } from "react";
import { useDispatch } from "react-redux";
import {
  Box,
  Typography,
  TextField,
  Slider,
  Button,
  FormControl,
  MenuItem,
  Select,
} from "@mui/material";
import { modifyText } from "../../store/reducers/text.reducer";
import { HexColorPicker } from "react-colorful";

const TextPanel = (props) => {
  const {
    id = "",
    uuid = "",
    content = "",
    position = {},
    type = "",
    size = 0,
    color = "",
    opacity = 1,
  } = props || null;

  const dispatch = useDispatch();
  const [currentId, setCurrentId] = useState(id);
  const [currentText, setCurrentText] = useState(content);
  const [currentFontSize, setCurrentFontSize] = useState(size);
  const [currentColor, setCurrentColor] = useState(color);
  const [currentOpacity, setCurrentOpacity] = useState(opacity);
  const [positionX, setPositionX] = useState(position.x);
  const [positionY, setPositionY] = useState(position.y);
  const [currentFontFamily, setCurrentFontFamily] = useState(type);
  const [currentLetterSpacing, setCurrentLetterSpacing] = useState(0);
  const [currentTextWrapping, setCurrentTextWrapping] = useState("normal");
  const [showPicker, setShowPicker] = useState(false);

  const togglePicker = () => {
    setShowPicker(!showPicker);
  };
  return (
    <Box
      sx={{
        margin: 2,
        padding: 2,
        border: "1px solid gray",
        borderRadius: 1,
        display: "flex",
        flexDirection: "column",
        gap: 2,
      }}
    >
      <Typography variant="h6">Text Option</Typography>
      <TextField
        label="Id"
        variant="standard"
        sx={{ mx: 3 }}
        value={currentId}
        onChange={(e) => {
          setCurrentId(e.target.value);
          dispatch(
            modifyText({
              uuid: uuid,
              id: e.target.value,
            })
          );
        }}
      />
      <TextField
        label="Text"
        variant="standard"
        sx={{ mx: 3 }}
        value={currentText}
        onChange={(e) => {
          setCurrentText(e.target.value);
          dispatch(
            modifyText({
              uuid: uuid,
              content: e.target.value,
            })
          );
        }}
      />
      <Button
        variant="contained"
        onClick={togglePicker}
        sx={{ mx: 3, backgroundColor: currentColor }}
      >
        {showPicker ? "Ok" : "Color"}
      </Button>
      {showPicker && (
        <div style={{ position: "relative" }}>
          <Box sx={{ mx: 5, position: "absolute", top: 0, zIndex: 100 }}>
            <HexColorPicker
              color={currentColor}
              onChange={setCurrentColor}
            />
          </Box>
        </div>
      )}
      <TextField
        label="Font-Family"
        variant="standard"
        sx={{ mx: 3 }}
        value={currentFontFamily}
        onChange={(e) => {
          setCurrentFontFamily(e.target.value);
          dispatch(
            modifyText({
              uuid: uuid,
              type: e.target.value,
            })
          );
        }}
      />
      <TextField
        label="Size"
        variant="standard"
        type="number"
        sx={{ mx: 3 }}
        value={currentFontSize}
        onChange={(e) => {
          setCurrentFontSize(e.target.value);
          dispatch(
            modifyText({
              uuid: uuid,
              size: parseInt(e.target.value),
            })
          );
        }}
      />
      <TextField
        label="X"
        variant="standard"
        type="number"
        sx={{ mx: 3 }}
        value={positionX}
        onChange={(e) => {
          setPositionX(e.target.value);
          dispatch(
            modifyText({
              uuid: uuid,
              position: { x: parseInt(e.target.value) },
            })
          );
        }}
      />
      <TextField
        label="Y"
        variant="standard"
        type="number"
        sx={{ mx: 3 }}
        value={positionY}
        onChange={(e) => {
          setPositionY(e.target.value);
          dispatch(
            modifyText({
              uuid: uuid,
              position: { y: parseInt(e.target.value) },
            })
          );
        }}
      />
      <Box sx={{ mx: 3 }}>
        <Typography gutterBottom>Opacity</Typography>
        <Slider
          aria-label="Default"
          value={currentOpacity}
          onChange={(e) => setCurrentOpacity(e.target.value)}
          valueLabelDisplay="auto"
        />{" "}
      </Box>
      <TextField
        label="Letter Spacing"
        type="number"
        value={currentLetterSpacing}
        onChange={(e) => setCurrentLetterSpacing(Number(e.target.value))}
        variant="standard"
        sx={{ mx: 3 }}
      />
      <FormControl sx={{ mx: 3 }}>
        <Select
          variant="standard"
          value={currentTextWrapping}
          onChange={(e) => setCurrentTextWrapping(e.target.value)}
        >
          <MenuItem value="normal">Normal</MenuItem>
          <MenuItem value="nowrap">No Wrap</MenuItem>
          <MenuItem value="pre">Pre</MenuItem>
          <MenuItem value="pre-line">Pre-line</MenuItem>
        </Select>
      </FormControl>
    </Box>
  );
};

export default TextPanel;
