import React, { useState, useEffect } from "react";
import {
  Box,
  TextField,
  FormControl,
  Select,
  MenuItem,
  Slider,
  Button,
} from "@mui/material";

import { useDispatch, useSelector } from "react-redux";
import { modifyText } from "../store/reducers/text.reducer";
import CanvasPanel from "../components/sidebar/CanvasPanel";
import TextPanel from "../components/sidebar/TextPanel";
import ImagePanel from "../components/sidebar/ImagePanel";

const Sidebar = (props) => {
  const dispatch = useDispatch();
  const { selection } = useSelector((state) => state.plan);
  const { texts } = useSelector((state) => state.text);
  const { images } = useSelector((state) => state.image);
  const [selectedItem, setSelectedItem] = useState({});
  const [status, setStatus] = useState("");

  useEffect(() => {
    if (selection.length === 1) {
      const selectedText = texts.find((text) => text.uuid === selection[0]);
      const selectedImage = images.find((image) => image.uuid === selection[0]);
      if (selectedText) {
        setSelectedItem(selectedText);
        setStatus("text");
      } else if (selectedImage) {
        setSelectedItem(selectedImage);
        setStatus("image");
      } else {
        setSelectedItem({});
      }
    } else {
      setStatus("");
    }
  }, [selection]);

  return (
    <Box
      sx={{
        display: "flex",
        flexDirection: "column",
        gap: 2,
        mb: 3,
      }}
    >
      {!selection.length && <CanvasPanel />}

      {status === "text" && <TextPanel {...selectedItem} />}
      {status === "image" && <ImagePanel {...selectedItem} />}
    </Box>
  );
};

export default Sidebar;
