import React, { useEffect, useRef, useState, useMemo } from "react";
import { useSelector, useDispatch } from "react-redux";
import "./canvas.css";
import { v4 as uuidv4 } from "uuid";
import * as d3 from "d3";
import {
  estimateTextWidth,
  imageBBox,
  positionInZone,
  testOverlap,
  textBBox,
} from "../utils/geometry";

import TextLayer from "./TextLayer";
import ImageLayer from "./ImageLayer";

import { setSelection } from "../store/reducers/plan.reducer";
import { addImage } from "../store/reducers/image.reducer";
import { addText } from "../store/reducers/text.reducer";
import { useSvg } from "../provider/SvgContext";

const Plan = ({ zoom }) => {
  const viewBox = `0 0 ${1000 / zoom} ${1000 / zoom}`;
  const plan = useSelector((state) => state.plan);
  const { selection } = plan;
  const { texts } = useSelector((state) => state.text);
  const { images } = useSelector((state) => state.image);
  const svg = useSvg();
  const { zoomTransform, tool } = plan;
  const [selecting, setSelecting] = useState(false);
  const [selectingStartPos, setSelectingStartPos] = useState({ x: 0, y: 0 });
  const [selectingCurrentPos, setSelectingCurrentPos] = useState({
    x: 0,
    y: 0,
  });

  const dispatch = useDispatch();
  const getSvgRect = () => svg.current.getBoundingClientRect();

  const selectionBoxes = useMemo(() => {
    const res = [];
    if (selection.length) {
      for (const text of texts) {
        if (selection.includes(text.uuid)) {
          const abox = textBBox(text);
          res.push(abox);
        }
      }
      for (const image of images) {
        if (selection.includes(image.uuid)) {
          const abox = imageBBox(image);
          res.push(abox);
        }
      }
    }
    return res;
  }, [selection, texts, images]);

  const onMouseDown = (event) => {
    const svgbox = getSvgRect();
    let targetPos = positionInZone(
      event.clientX - svgbox.x,
      event.clientY - svgbox.y,
      zoomTransform
    );

    if (
      targetPos.x < 0 ||
      targetPos.y < 0 ||
      targetPos.x > plan.size.width ||
      targetPos.y > plan.size.height
    )
      return;

    switch (tool) {
      case "select":
        setSelecting(true);
        setSelectingStartPos({ x: targetPos.x, y: targetPos.y });
        setSelectingCurrentPos({ x: targetPos.x, y: targetPos.y });
        break;
      case "text":
        const width = estimateTextWidth("Text", 32);
        const height = 32;
        if (
          targetPos.x - width / 2 < 0 ||
          targetPos.x + width / 2 > plan.size.width ||
          targetPos.y - height / 2 < 0 ||
          targetPos.y + height / 2 > plan.size.height
        )
          return;
        dispatch(
          addText({
            uuid: uuidv4(),
            id: "text",
            content: "text",
            size: 32,
            color: "#000",
            opacity: 1,
            position: {
              x: targetPos.x - width / 2,
              y: targetPos.y,
            },
            type: "sans-serif",
          })
        );
        break;
      case "image":
        const w = 50,
          h = 50;
        if (
          targetPos.x - w / 2 < 0 ||
          targetPos.x + w / 2 > plan.size.width ||
          targetPos.y - h / 2 < 0 ||
          targetPos.y + h / 2 > plan.size.height
        )
          return;
        dispatch(
          addImage({
            uuid: uuidv4(),
            id: "image",
            url: "/assets/images/image.jpg",
            position: {
              x: targetPos.x - w / 2,
              y: targetPos.y - h / 2,
            },
            scale: 1,
            width: w,
            height: h,
          })
        );
        break;
      default:
        break;
    }
  };

  const onMouseMove = (event) => {
    const svgbox = getSvgRect();
    let pos = positionInZone(
      event.clientX - svgbox.x,
      event.clientY - svgbox.y,
      zoomTransform
    );
    if (
      pos.x < 0 ||
      pos.y < 0 ||
      pos.x > plan.size.width ||
      pos.y > plan.size.height
    ) {
      return;
    }
    if (tool === "select") {
      if (selecting) {
        setSelectingCurrentPos({ x: pos.x, y: pos.y });
        let uuids = [];
        const xmin = Math.min(selectingStartPos.x, selectingCurrentPos.x);
        const xmax = Math.max(selectingStartPos.x, selectingCurrentPos.x);
        const ymin = Math.min(selectingStartPos.y, selectingCurrentPos.y);
        const ymax = Math.max(selectingStartPos.y, selectingCurrentPos.y);

        for (const text of texts) {
          if (testOverlap(text, xmin, ymin, xmax, ymax)) {
            uuids.push(text.uuid);
          }
        }

        for (const image of images) {
          if (testOverlap(image, xmin, ymin, xmax, ymax)) {
            uuids.push(image.uuid);
          }
        }
        dispatch(setSelection({ uuids }));
      }
    }
  };

  const onMouseUp = (event) => {
    if (tool === "select") {
      setSelecting(false);
      let uuids = [];
      const xmin = Math.min(selectingStartPos.x, selectingCurrentPos.x);
      const xmax = Math.max(selectingStartPos.x, selectingCurrentPos.x);
      const ymin = Math.min(selectingStartPos.y, selectingCurrentPos.y);
      const ymax = Math.max(selectingStartPos.y, selectingCurrentPos.y);

      for (const text of texts) {
        if (testOverlap(text, xmin, ymin, xmax, ymax)) {
          uuids.push(text.uuid);
        }
      }

      for (const image of images) {
        if (testOverlap(image, xmin, ymin, xmax, ymax)) {
          uuids.push(image.uuid);
        }
      }
      dispatch(setSelection({ uuids }));
    }
  };

  return (
    <svg
      id="svg"
      width={plan.size.width}
      height={plan.size.height}
      onMouseDown={onMouseDown}
      onMouseMove={onMouseMove}
      onMouseUp={onMouseUp}
      ref={svg}
      style={{
        width: "100%",
        height: "100%",
        backgroundColor: "rgb(151, 162, 182)",
        userSelect: "none",
      }}
    >
      <rect
        width={plan.size.width}
        height={plan.size.height}
        fill="#fcfcfc"
      ></rect>
      {plan.backgroundURL && <image href={plan.backgroundURL}></image>}
      <TextLayer />
      <ImageLayer />
      {tool === "select" && selecting && (
        <rect
          className="selection-area"
          x={Math.min(selectingStartPos.x, selectingCurrentPos.x)}
          y={Math.min(selectingStartPos.y, selectingCurrentPos.y)}
          width={
            Math.max(selectingStartPos.x, selectingCurrentPos.x) -
            Math.min(selectingStartPos.x, selectingCurrentPos.x)
          }
          height={
            Math.max(selectingStartPos.y, selectingCurrentPos.y) -
            Math.min(selectingStartPos.y, selectingCurrentPos.y)
          }
        ></rect>
      )}
      {selectionBoxes.map((box, index) => (
        <rect
          key={index}
          className="selection-box"
          x={box.x - 1.5}
          y={box.y - 1.5}
          width={box.width + 3}
          height={box.height + 3}
          fill="none"
        ></rect>
      ))}
    </svg>
  );
};

export default Plan;
